<template>
    <div class="card mt-2 question-card">
        <div class="card-body">
            <p class="card-text">
                Q: {{question.text}}
                <br>
                A: {{answers_text_or_not_answered}}
            </p>


            <button
                    class="btn btn-success mr-2 tooltip-big-text"
                    v-if="show_derive_button"
                    v-on:click="onDerive"
                    data-balloon-pos="up"
                    data-balloon-length="fit"
                    aria-label="This button is for step 3: modify your existing question and derive a new one. Please make sure your new question has different answers than the old question.">
                Derive New Question
            </button>

            <button
                    class="btn btn-danger mr-2 tooltip-big-text"
                    v-if="show_answer_default_button"
                    v-on:click="onEdit"
                    data-balloon-pos="up"
                    data-balloon-length="fit"
                    aria-label="You need to answer this question before proceeding to the next passage.">
                Answer this default question
            </button>

            <button
                    v-if="show_edit_button"
                    class="btn btn-warning mr-2 tooltip-big-text"
                    v-on:click="onEdit"
                    data-toggle="tooltip"
                    data-balloon-pos="up"
                    aria-label="This button is to edit this question and overwrite the old one.">
                Edit
            </button>

            <button
                    class="btn btn-danger"
                    v-if="show_delete_button"
                    v-on:click="onDelete"
            >
                Delete
            </button>

        </div>
    </div>
</template>

<script>
    export default {
        name: 'QuestionCard',
        components: {

        },
        props: ['question', 'answer_text'],

        computed: {

            answers_text_or_not_answered: function(){
                if (this.question.is_default_question){
                    if (this.question.is_answered){
                        return this.answer_text;
                    }else{
                        return "Not answered.";
                    }
                }else{
                    return this.answer_text;
                }
            },

            show_edit_button: function(){
                return !this.question.is_default_question || (this.question.is_default_question && this.question.is_answered);
            },

            show_derive_button: function(){
                return !this.question.is_default_question;
            },

            show_answer_default_button: function(){
                return this.question.is_default_question && !this.question.is_answered;
            },

            show_delete_button: function(){
                return !this.question.is_default_question;
            },


        },

        methods: {
            onDelete: function(){
                this.$emit("delete");
            },

            onEdit: function(){
                this.$emit("edit");
            },

            onDerive: function(){
                this.$emit("derive");
            },

        },
    }
</script>

